window.addEventListener('DOMContentLoaded', () => {
    gsap.registerPlugin(ScrollTrigger);
    if(document.querySelector(".index-page")){
        indexPageAnim()

    }

    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        $(".fixed-body2").css("padding-right",`${getScrollBarSize()[0]}px`)
    })
})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}


function indexPageAnim() {
    gsap.from(".header", {
        opacity: 0,
        yPercent: -150,
        duration: 2,
        ease: "power4"
    })

    gsap.from(".main .container", {
        opacity: 0,
        duration: 2,
        ease: "power4"
    })

    infoBlocksAnim()
    variantItemAnim()

    let combineCards = document.querySelectorAll(".combine-card")
    cardAnimation(combineCards)
    cardAnimation(document.querySelectorAll(".gallery"))

    let productionCards = document.querySelectorAll(".production-card")
    cardAnimation(productionCards)

    let forms = document.querySelectorAll("._form")
    cardAnimation(forms)

    let workCards = document.querySelectorAll(".work-card")
    cardAnimation(workCards)

    cardAnimation(document.querySelectorAll(".production__grid"))
    cardAnimation(document.querySelectorAll(".steps__grid"))
    cardAnimation(document.querySelectorAll(".conditions__img"))
    cardAnimation(document.querySelectorAll(".conditions__img2"))
    cardAnimation(document.querySelectorAll(".debt__sale"))
    cardAnimation(document.querySelectorAll(".test__img"))

    let reviewCards = document.querySelectorAll(".review-card")
    cardAnimation(reviewCards)

    let infoCards = document.querySelectorAll(".info-card")
    cardAnimation(infoCards)
}

function infoBlocksAnim() {
    let infoBlocks = document.querySelectorAll(".info-block")

    infoBlocks.forEach(infoBlock => {
        let title = infoBlock.querySelector(".text96")
        let subTitle = infoBlock.querySelector(".text64")
        let subTitle2 = infoBlock.querySelector(".text48")
        let subTitle3 = infoBlock.querySelector(".text24")

        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: infoBlock,
                start: 'top bottom',
            }
        })

        tl.from(title, {
            x: -100,
            opacity: 0,
            duration: 2,
            ease: "power4"
        }, 0.4)
        .from(subTitle, {
            x: 100,
            opacity: 0,
            duration: 2,
            ease: "power4"
        },'<')
        .from(subTitle2, {
            x: -100,
            opacity: 0,
            duration: 2,
            ease: "power2"
        },'<')
        .from(subTitle3, {
            x: 100,
            opacity: 0,
            duration: 2,
            ease: "power4"
        },'<')
    })
}

function variantItemAnim() {
    let cards = document.querySelectorAll(".variant-info__item")

    cards.forEach(card => {
        let icon = card.querySelector(".variant-info__icon")
        let text = card.querySelector(".variant-info__item-text")

        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: card,
                start: 'top bottom',
            }
        })

        tl.from(icon, {
            opacity: 0,
            duration: 2,
            ease: "power4"
        }, 0.4)
            .from(text, {
                x: 500,
                opacity: 0,
                duration: 2,
                ease: "power4"
            },'<')
    })
}

function cardAnimation(card) {
    gsap.from(card, {
        // yPercent: 40,
        opacity: 0,
        duration: 3,
        scrollTrigger: {
            trigger: card,
            start: 'top bottom',
            toggleActions: 'play none none reverse'
        }
    })
}
